<template>
  <div id="app">
    <!-- <div id="nav">
      <router-link to="/">Home</router-link> |
      <router-link to="/about">About</router-link>
    </div>
    <router-view/> -->
    <!-- <img alt="Vue logo" src="./assets/logo.png">
    <h1>{{title}}</h1>
    <div v-if="nowpage == 0">page0</div>
    <div v-else>page1</div> -->
    <div class="weibo">
      <div class="card" v-for="item in items">
        <div class="card_top">
          <div class="toxiang" :style="{backgroundImage:'url(' + item.toxiang + ')'}"></div>
          <div class="word">
              <div class="name" :class="{'is-vip':item.vip}">{{item.name}}</div>
              <div class="time">{{item.time}}</div>      
          </div>
        </div>
        <div class="card_text">{{item.text}}</div>
        <img  alt="" v-if="item.imgshow" :src="item.imageSrc">
        <div class="card-bottom">
          <div class="card-xinxi">
            <img src="./assets/xinxi.png" alt="">
            <p>{{item.xinxi}}</p>
          </div>
          <div class="card-zan" @click="dianzan(item.index)">
            <img :src="item.dzimg" alt="">
            <p>{{item.dianzan}}</p>
          </div>
        </div>
      </div>
      <div class="bottom">
        <input type="text" v-model="sendText" placeholder="请先输入内容">
        <button @click="send">发表</button>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue'

export default {
  name: 'app',
  components: {
    HelloWorld
  },
  data:function(){
    return{
      items:[],
      sendText:''
    }
  },
  created(){
    window.apiready = function(){
      var header = $api.byId("app");
      $api.fixStatusBar(header);
    }

    var _this = this;
    var data = [{
        index:0,
        toxiang:require("./assets/img1.jpg"),
        name:"孙悟空",
        time:"1小时前 华为",
        text:"大家好，我是孙悟空！",
        imageSrc:require("./assets/img1.jpg"),
        imgshow:true,
        vip:true,
        xinxi:2,
        dzimg:require("./assets/dianzan.png"),
        dianzan:50
      },{
        index:1,
        toxiang:require("./assets/img2.jpg"),
        name:"康师傅",
        time:"1小时前 小米",
        text:"大家好，我是康师傅！",
        imageSrc:require("./assets/img2.jpg"),
        imgshow:true,
        vip:false,
        xinxi:2,
        dzimg:require("./assets/dianzan.png"),
        dianzan:50
      }
    ]
    _this.items = data;
  },
  methods:{
    dianzan:function(index){
      var _this = this;
      if(_this.items[index].dzimg == require("./assets/dianzan2.png")) {
        _this.items[index].dzimg = require("./assets/dianzan.png");
        _this.items[index].dianzan = _this.items[index].dianzan - 1;
      }else{
        _this.items[index].dzimg = require("./assets/dianzan2.png");
        _this.items[index].dianzan = _this.items[index].dianzan + 1;
      }
    },
    send:function(){
      var _this = this;
      var num = _this.items.length;
      var sendData = {
        index:num,
        toxiang:require("./assets/img2.jpg"),
        name:"康师傅",
        time:"1小时前 小米",
        text:_this.sendText,
        imageSrc:"./assets/img2.jpg",
        imgshow:false,
        vip:false,
        xinxi:2,
        dzimg:require("./assets/dianzan.png"),
        dianzan:50
      }
      if(_this.sendText == "") return;
      _this.items.push(sendData);
    }
  },
}
</script>

